/* General styling */
body,html{height:100%;font-family:sans-serif}
a{margin:0;transition:all .4s;-webkit-transition:all .4s;-o-transition:all .4s;-moz-transition:all .4s}
a:focus{outline:none!important}
a:hover{text-decoration:none}

.App {
  text-align: center;
}

/* Navigation bar styling */
.nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color:#36304a;
}
li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
li {
  float: left;
}
li a:hover {
  background-color: #463f5f;
}

/* Table styling */
.limiter{width:100%;margin:20px 20px}
.wrap-table100{width:1170px}
table{border-spacing:1;border-collapse:collapse;background:#fff;border-radius:10px;overflow:hidden;width:100%;margin:0 auto;position:relative}
table *{position:relative}
table td,table th{padding-left:8px}
table thead tr{height:60px;background:#36304a}
table tbody tr{height:50px}
table tbody tr:last-child{border:0}
table td,table th{text-align:left}
table td.l,table th.l{text-align:right}
table td.c,table th.c{text-align:center}
table td.r,table th.r{text-align:center}
.tableHead th{font-family:OpenSans-Regular;font-size:18px;color:#fff;line-height:1.2;font-weight:unset}
tbody tr:nth-child(odd){background-color:#f5f5f5}
tbody tr{font-family:OpenSans-Regular;font-size:15px;color:gray;line-height:1.2;font-weight:unset}
tbody tr:hover{color:#555;background-color:#f5f5f5;cursor:pointer}
.column1{width:260px;padding-left:40px}
.column2{width:160px}
.column3{width:245px}
.column4{width:110px;text-align:right}
.column5{width:170px;text-align:right}
.column6{width:222px;text-align:right;padding-right:62px}